body {
  font-family: "Helvetica", sans-serif;
  -webkit-font-smoothing:antialiased;

}

.sidenav {
  width: 130px;
  position: fixed;
  z-index: 1;
  top: 20px;
  left: 10px;
  background: #eee;
  overflow-x: hidden;
  padding: 8px 0;
}

.sidenav a {
  padding: 6px 8px 6px 16px;
  text-decoration: none;
  color: #2196F3;
  display: block;
}

.sidenav a:hover {
  color: #064579;
}

.main {
  margin-left: 140px; /* Same width as the sidebar + left position in px */
  font-size: 16px; /* Increased text to enable scrolling */
  padding: 0px 10px;
}

@media screen and (max-height: 450px) {
  .sidenav {padding-top: 15px;}
  .sidenav a {font-size: 18px;}
}

#baby {
  height:130px;
  width:130px;
  border-radius:100px;
  text-align: center;
  font-size:20px;
  cursor:pointer;
  transition:all 0.2s ease-in-out;
}

#baby:hover{
  transform:scale(1.2)
}
#baby:active{
  transform:scale(1.5)
}
#count{
  padding-top:55px;
}

.shop{
  background-color:red
}
.item{
  display:flex;
  background-color:rgba(0,0,0,0.1);
  width:300px;
  justify-content: left;
  font-size:14px;
  height:75px;
  cursor:pointer;
}

.item button{
  font-size:14px;
  border:2px solid rgba(0,0,0,0.1);
  cursor:pointer;
  outine:none;
}

.item img{
  height:75px;
  margin-right:20px
}
